body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background: linear-gradient(90deg, #0062ff, #da61ff);
    height: 100vh;
    width: 98vw;
}

.admin-container{
    width: 80%;
    margin: 0 auto;
    overflow: hidden;
    border-radius: 5px;
}

.admin-header {
    color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    background: #333;
    padding: 20px;
    border-radius: 5px;
    margin: 20px 0;
    text-decoration: none;

}

.admin-header h1 {
    margin: 0;
    padding: 20px 0;
    font-size: 2.5em;
}

.test-details {
    background: #fff;
    color: #333;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin: 20px 0;
    text-decoration: none;
}

.questions {
    background: #fff;
    color: #ffffff;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin: 20px 0;
}

.label {
    color: #fff;
    font-size: 1.5em;
    margin: 10px 0;
}

.input {
    width: 80%;
    padding: 10px;
    margin: 10px 0;
    border-radius: 5px;
    border: 1px solid #333;
}



.input:focus {
    outline: none;
}

.button {
    background: #fff;
    color: #333;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin: 20px 0;
}

.button:hover {
    background: #333;
    color: #fff;
}

.userName_Container {
    display: flex;
    flex-direction: column;
    margin-top: 20px;
    width: 100%;
}

.swipeDown  {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.questions h2 {
    margin: 0;
    padding: 20px 0;
    font-size: 2em;
}

.options_container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
}

.question{
    background: #fff;
    color: #333;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin: 20px 0;
    text-decoration: none;
}